<template>
  <div>
    <md-subheader class="list-title" v-if="title">{{title}}</md-subheader>
    <div class="photo-items">
      <div
        class="photo-item"
        :key="people.id"
        v-for="people in peoples"
        @click="onPressPerson(people)"
      >
        <md-avatar :class="avatarClasses">
          <img :src="`${apiUrl}${people.face}`" alt="People">
        </md-avatar>
        <div class="photo-title" v-if="useNames">
          {{people.name}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HumansList',
  props: {
    title: String,
    peoples: Array,
    apiUrl: String,
    useNames: Boolean,
    avatarClasses: {
      type: Array,
      default: () => [],
    },
  },
  methods: {
    onPressPerson(person) {
      this.$emit('press-person', person);
    },
  },
};
</script>

<style>
  .photo-items {
    display: flex;
    overflow: scroll;
    flex-wrap: nowrap;
    align-items: flex-start;
  }

  .photo-item {
    margin: 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .photo-title {
    margin-top: 5px;
  }
</style>

