import Humans from '@/api/Human';

export const actions = {
  getHumans: 'modules/humans/getHumans',
};

export const mutations = {
  getHumans: 'moudles/humans/GET_HUMANS',
};

export default {
  state: {
    list: [],
  },
  mutations: {
    [mutations.getHumans](state, payload) {
      state.list = payload;
    },
  },
  actions: {
    async [actions.getHumans](ctx) {
      const humans = await Humans.getAll();
      ctx.commit(mutations.getHumans, humans);
    },
  },
  getters: {
    humans(state) {
      const humans = {
        members: [],
        strangers: [],
      };

      state.list.forEach((human) => {
        if (human.type === 0) {
          humans.members.push(human);
          return;
        }
        humans.strangers.push(human);
      });
      return humans;
    },
  },
};

