import Api from './Api';

export class Human extends Api {
  plural = '/humans'

  getFaces(id) {
    return this.request('get', `${this.plural}/${id}/faces`);
  }

  uploadFace(id, data) {
    const options = { data };
    return this.request('put', `${this.plural}/${id}/faces`, options);
  }

  mergeHuman(id, humans) {
    const options = { data: humans };
    return this.request('post', `${this.plural}/${id}/merge`, options);
  }
}

export default new Human();
