import Vue from 'vue';
import Router from 'vue-router';
import Humans from './views/Humans.vue';
import Human from './views/Human.vue';
import NewHuman from './views/NewHuman.vue';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'humans',
      component: Humans,
    },
    {
      path: '/human/:id',
      name: 'human',
      component: Human,
    },
    {
      path: '/human',
      name: 'newHuman',
      component: NewHuman,
    },
  ],
});
