import axios from 'axios';

export default class Api {
  static url = `${process.env.VUE_APP_HOST}:${process.env.VUE_APP_PORT}`
  plural = ''

  getAll() {
    return this.request('get', this.plural);
  }

  create(data) {
    const options = { data };
    return this.request('post', `${this.plural}`, options);
  }

  getById(id) {
    return this.request('get', `${this.plural}/${id}`);
  }

  updateById(id, data) {
    const options = { data };
    return this.request('patch', `${this.plural}/${id}`, options);
  }

  deleteById(id) {
    return this.request('delete', `${this.plural}/${id}`);
  }

  request(method, assetUrl, options) {
    return axios({
      url: `${this.constructor.url}${assetUrl}`,
      method,
      ...options,
    }).then(response => response.data);
  }
}

