<template>
  <md-app md-mode="reveal">
    <md-app-toolbar>
      <div class="md-toolbar-row">
        <custom-header
          :is-saving="isSaving"
          @press-cancel="onPressCancel"
          @press-save="onPressSave"
        />
      </div>
    </md-app-toolbar>
    <md-app-content ref="content">
      <div class="md-layout avatar-section">
        <md-avatar class="large md-avatar-icon" id="human-avatar">
          <img :src="fileData.fileUri">
          <div class="upload-control">
            <file-uploader text="add photo" @load-file="onUpload" />
          </div>
        </md-avatar>
      </div>
      <div class="md-layout">
        <div class="md-layout-item">
          <md-field>
            <label>Name</label>
            <md-input v-model="humanName" />
          </md-field>
        </div>
      </div>
    </md-app-content>
  </md-app>
</template>

<script>
import CustomHeader from '@/components/NewHumanHeader.vue';
import FileUploader from '@/components/FileUploader.vue';
import { actions } from '../modules/human';

export default {
  data() {
    return {
      humanName: '',
      isSaving: false,
      fileData: {
        file: {},
        fileUri: '',
      },
    };
  },
  methods: {
    onUpload(fileData) {
      this.fileData = fileData;
    },

    onPressCancel() {
      this.$router.replace('/');
    },

    uploadFace(humanId) {
      const fd = new FormData();
      fd.append('face', this.fileData.file);
      return this.$store.dispatch(actions.uploadFace, {
        data: fd,
        humanId,
      });
    },

    async onPressSave() {
      this.isSaving = true;
      const human = await this.$store.dispatch(actions.createHuman, {
        name: this.humanName,
        type: 0,
      });
      await this.uploadFace(human.id);
      this.$router.replace(`/human/${human.id}`);
      this.isSaving = false;
    },
  },
  components: {
    CustomHeader,
    FileUploader,
  },
};
</script>

<style>
  #human-avatar {
    background-color: lightgray;
  }
  .upload-control {
    position: absolute;
  }
</style>

