<template>
  <div class="md-toolbar-row">
    <md-button class="md-primary" @click="onPressCancel">
      Cancel
    </md-button>
    <div class="md-toolbar-section-end">
      <md-button :disabled="isSaving" class="md-primary" @click="onPressSave">
        Save
        <md-progress-spinner
          v-if="isSaving"
          :md-stroke="2"
          :md-diameter="10"
          md-mode="indeterminate"
        />
      </md-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'NewHumanHeader',
  props: {
    isSaving: Boolean,
  },
  methods: {
    onPressCancel() {
      this.$emit('press-cancel');
    },
    onPressSave() {
      this.$emit('press-save');
    },
  },
};
</script>
