<template>
  <md-app md-mode="reveal">
    <md-app-toolbar class="md-large">
      <div class="md-toolbar-row"></div>
      <div class="md-toolbar-row">
        <span class="md-title title">
          Peoples
        </span>
        <router-link to="/human">
          Add member
        </router-link>
      </div>
    </md-app-toolbar>
    <md-app-content>
      <people-list :apiUrl="apiUrl" :peoples="humans.members" @press-person="onPressPerson" />
      <people-items
        title="Strangers"
        :apiUrl="apiUrl"
        :peoples="humans.strangers"
        @press-person="onPressPerson"
      />
    </md-app-content>
  </md-app>
</template>

<script>
import { actions } from '@/modules/humans';
import PeopleList from '@/components/PeopleList.vue';
import PeopleItems from '@/components/PeopleItems.vue';

export default {
  name: 'home',
  created() {
    this.$store.dispatch(actions.getHumans);
  },
  computed: {
    humans() {
      return this.$store.getters.humans;
    },
    apiUrl() {
      return this.$store.getters.apiUrl;
    },
  },
  methods: {
    onPressPerson(person) {
      this.$router.push(`/human/${person.id}`);
    },
  },
  components: {
    PeopleList,
    PeopleItems,
  },
};
</script>

<style>
  .title{
    flex: 1
  }
</style>
