<template>
  <md-dialog :md-active="dialog" @update:mdActive="onChangeActive" class="members-list">
    <md-dialog-content>
      <md-subheader class="human-selector-popup-header">
        <span class="human-selector-popup-header-title">Who's in this photos?</span>
        <md-button class="md-icon-button md-raised" @click="onChangeActive(false)">
          <md-icon>close</md-icon>
        </md-button>
      </md-subheader>
      <people-items
        :avatarClasses="['md-large']"
        :useNames="true"
        :apiUrl="apiUrl"
        :peoples="members"
        @press-person="onPressPerson"
      />
    </md-dialog-content>
  </md-dialog>
</template>

<script>
import PeopleItems from './PeopleItems.vue';

export default {
  props: {
    dialog: Boolean,
    members: Array,
    apiUrl: String,
  },
  methods: {
    onChangeActive(active) {
      this.$emit('update:dialog', active);
    },
    onPressPerson(person) {
      this.$emit('press-person', person);
    },
  },
  components: {
    PeopleItems,
  },
};
</script>

