<template>
  <div class="file-uploader">
    <md-button class="md-primary upload-file-button">
      <label for="file-uploader">
        {{text}}
        <input
          name="file-uploader"
          id="file-uploader"
          type="file" @input="onInputFile"
          :accept="accept"
        />
      </label>
    </md-button>
  </div>
</template>

<script>
export default {
  name: 'FileUploader',
  props: {
    text: String,
    accept: {
      type: String,
      default: 'image/png,image/jpeg',
    },
  },
  methods: {
    onInputFile(el) {
      const { files } = el.target;
      if (!files || !files[0]) {
        return;
      }
      const [file] = files;
      this.readFile(file)
        .then((fileUri) => {
          this.$emit('load-file', {
            file,
            fileUri,
          });
        });
    },

    readFile(file) {
      return new Promise((res, rej) => {
        const FR = new FileReader();

        FR.addEventListener('loadend', (e) => {
          res(e.target.result);
        });
        FR.addEventListener('onerror', (err) => {
          rej(err);
        });
        FR.readAsDataURL(file);
      });
    },
  },
};
</script>

<style>
  #file-uploader {
    display: none;
  }

  .file-uploader .md-button-content {
    width: 100%;
    height: 100%;
  }

  .file-uploader .md-button-content label {
    width: 100%;
    height: 100%;
    align-items: center;
    display: flex;
    justify-content: center;
  }
</style>
