<template>
  <md-app md-mode="reveal">
    <md-app-toolbar>
      <div class="md-toolbar-row">
        <custom-header
          :rightMenuItems="rightMenu"
          @press-before="onPressBack"
          @press-right--menu="onPressMenu"
        />
      </div>
    </md-app-toolbar>
    <md-app-content ref="content">
      <md-progress-bar v-if="humanLoading" md-mode="indeterminate" class="human-progress" />
      <md-empty-state
        v-if="humanFinded === false"
        md-icon="priority_high"
        md-label="Person not found"
        md-description="You can try open another person."
      >
        <md-button class="md-primary md-raised" @click="onPressBack">Go back</md-button>
      </md-empty-state>
      <div v-if="humanFinded === true">
        <div class="md-layout avatar-section">
          <md-avatar class="large">
            <img :src="`${apiUrl}${human.info.face}`" alt="People">
          </md-avatar>
          <div class="md-layout-item edit-avatar">
            <file-uploader
              text="Edit"
              @load-file="uploadFace"
            />
          </div>
        </div>
        <div class="md-layout">
          <div class="md-layout-item">
            <md-field>
              <label>Name</label>
              <md-input v-model="humanName" />
              <md-button
                v-if="human.info.type === 1"
                class="md-icon-button md-primary"
                @click="openDialogMembers = true"
              >
                <md-icon>add</md-icon>
              </md-button>
            </md-field>
          </div>
        </div>
        <photo-gallery
          :apiUrl="apiUrl"
          :faces="human.humanFaces"
          :selectedFaces.sync="selectedFaces"
        />
        <md-snackbar
          :md-duration="1000"
          :md-active.sync="snackbar.show"
          md-persistent
        >
          <span>{{snackbar.message}}</span>
        </md-snackbar>
        <md-snackbar
          :md-duration="Infinity"
          :md-active="selectedFaces.length > 0"
          md-persistent
        >
          <span>Drop {{selectedFaces.length}} photos?</span>
          <div>
            <md-button class="md-primary" @click="onCancelDropPhotos">Cancel</md-button>
            <md-button class="md-accent" @click="onConfirmDropPhotos">Delete</md-button>
          </div>
        </md-snackbar>
        <human-selector-popup
          @press-person="onPressMember"
          :dialog.sync="openDialogMembers"
          :members="humans.members"
          :apiUrl="apiUrl"
        />
        <md-dialog-confirm
          :md-active.sync="confirm.show"
          md-title="Are you sure?"
          :md-content="confirm.message"
          md-confirm-text="Yes"
          md-cancel-text="No"
          @md-cancel="onCancelConfirm"
          @md-confirm="onConfirm"
        />
      </div>
    </md-app-content>
  </md-app>
</template>

<script>
import CustomHeader from '@/components/Header.vue';
import FileUploader from '@/components/FileUploader.vue';
import HumanSelectorPopup from '@/components/HumanSelectorPopup.vue';
import PhotoGallery from '@/components/PhotoGallery.vue';
import { actions } from '../modules/human';
import { actions as humanActions } from '../modules/humans';

export default {
  name: 'Human',
  data: () => ({
    humanLoading: true,
    humanFinded: null,
    snackbar: {
      show: false,
      message: '',
    },
    selectedFaces: [],
    confirm: {
      show: false,
      message: '',
      params: {},
    },
    openDialogMembers: false,
    name: '',
    nameUploadTimeout: 0,
    rightMenu: [
      {
        value: 'deleteMember',
        label: 'Delete Member',
      },
    ],
  }),
  async created() {
    const humanId = this.$route.params.id;
    try {
      const human = await this.$store.dispatch(actions.getHuman, humanId);
      if (human.type === 1) {
        this.$store.dispatch(humanActions.getHumans);
      }
      this.humanFinded = true;
    } catch (e) {
      this.humanFinded = false;
    } finally {
      this.humanLoading = false;
    }
  },
  destroyed() {
    this.$store.dispatch(actions.resetHuman);
  },
  methods: {
    onPressBack() {
      this.$router.replace('/');
    },
    updateName(name) {
      if (this.humanName === name) {
        return;
      }

      const data = { name };
      this.$store.dispatch(actions.setHumanInfo, data);

      if (this.nameUploadTimeout) {
        clearTimeout(this.nameUploadTimeout);
      }

      this.nameUploadTimeout = setTimeout(() => {
        this.$store.dispatch(actions.updateHuman, {
          humanId: this.$route.params.id,
          data,
        });
        this.snackbar = {
          show: true,
          message: 'Name saved',
        };
      }, 1000);
    },
    onPressMenu(value) {
      const humanId = this.$route.params.id;
      switch (value) {
        case 'deleteMember':
          this.$store
            .dispatch(actions.deleteHuman, humanId)
            .then(() => {
              this.$router.replace('/');
            });
          break;
        default:
      }
    },
    async uploadFace(fileData) {
      const humanId = this.$route.params.id;
      const fd = new FormData();
      fd.append('face', fileData.file);
      await this.$store.dispatch(actions.uploadFace, {
        data: fd,
        humanId,
      });
      this.snackbar = {
        show: true,
        message: 'Avatar updated',
      };
      this.$store.dispatch(actions.getHuman, humanId);
    },

    onPressMember(member) {
      const humanId = member.id;
      const data = [{ ...this.human.info, name: member.name }];
      this.confirm = {
        params: { humanId, data },
        message: `Merge ${this.human.info.name} into ${member.name}`,
        show: true,
      };
      this.openDialogMembers = false;
    },

    onCancelConfirm() {
      this.confirm = {
        show: false,
        message: '',
        params: {},
      };
      this.openDialogMembers = true;
    },

    onConfirm() {
      this.$store.dispatch(actions.mergeHuman, this.confirm.params)
        .then(() => {
          this.$router.replace(`/human/${this.confirm.params.humanId}`);
        });
    },

    onCancelDropPhotos() {
      this.selectedFaces = [];
    },

    onConfirmDropPhotos() {
      const urls = this.selectedFaces.map(url => this.$store.dispatch(actions.deletePhoto, url));
      Promise.all(urls)
        .then(() => {
          const humanId = this.$route.params.id;
          this.$store.dispatch(actions.getHuman, humanId);
        });
    },
  },
  computed: {
    human() {
      return this.$store.state.human;
    },
    apiUrl() {
      return this.$store.getters.apiUrl;
    },
    humans() {
      return this.$store.getters.humans;
    },
    humanName: {
      get() {
        return this.$store.state.human.info.name || '';
      },
      set(newName) {
        this.updateName(newName);
      },
    },
  },
  components: {
    CustomHeader,
    FileUploader,
    HumanSelectorPopup,
    PhotoGallery,
  },
};
</script>

<style>
  .human-progress {
    position: absolute;
    width: 100%;
    left: 0;
    top: 57px;
  }
  .avatar-section {
    min-height: 40vh;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .edit-avatar {
    text-align: center
  }

  .md-avatar.large {
    min-width: 200px;
    min-height: 200px;
    border-radius: 100px;
  }
</style>

