import Humans from '../api/Human';

export const actions = {
  getHumanInfo: 'modules/human/getHumanInfo',
  setHumanInfo: 'modules/human/setHumanInfo',
  updateHuman: 'modules/human/updateHuman',
  getFaces: 'modules/human/getFaces',
  setFaces: 'modules/human/setFaces',
  getHuman: 'modules/human/getHuman',
  deleteHuman: 'modules/human/deleteHuman',
  createHuman: 'modules/human/createHuman',
  uploadFace: 'modules/human/uploadFace',
  mergeHuman: 'modules/human/mergeHuman',
  deletePhoto: 'modules/human/deletePhoto',
  resetHuman: 'modules/human/resetHuman',
};

export const mutations = {
  setHumanInfo: 'moudles/human/SET_HUMAN_INFO',
  setFaces: 'moudles/human/SET_FACES',
  resetHuman: 'moudles/human/RESET_HUMAN',
};

export default {
  state: {
    info: {},
    humanFaces: [],
  },
  mutations: {
    [mutations.setHumanInfo](state, payload) {
      state.info = {
        ...state.info,
        ...payload,
      };
    },
    [mutations.setFaces](state, payload) {
      state.humanFaces = payload;
    },
    [mutations.resetHuman](state) {
      state.info = {};
      state.humanFaces = [];
    },
  },
  actions: {
    async [actions.getHumanInfo](ctx, payload) {
      const human = await Humans.getById(payload);
      ctx.dispatch(actions.setHumanInfo, human);
      return human;
    },
    async [actions.setHumanInfo](ctx, payload) {
      ctx.commit(mutations.setHumanInfo, payload);
    },
    async [actions.getFaces](ctx, payload) {
      const faces = await Humans.getFaces(payload);
      ctx.dispatch(actions.setFaces, faces);
      return faces;
    },
    async [actions.setFaces](ctx, payload) {
      ctx.commit(mutations.setFaces, payload);
    },
    [actions.getHuman](ctx, payload) {
      return Promise.all([
        ctx.dispatch(actions.getHumanInfo, payload),
        ctx.dispatch(actions.getFaces, payload),
      ]);
    },
    [actions.deleteHuman](ctx, payload) {
      return Humans.deleteById(payload);
    },
    [actions.updateHuman](ctx, payload) {
      return Humans.updateById(payload.humanId, payload.data);
    },
    [actions.createHuman](ctx, payload) {
      return Humans.create(payload);
    },
    [actions.uploadFace](ctx, payload) {
      return Humans.uploadFace(payload.humanId, payload.data);
    },
    [actions.mergeHuman](ctx, payload) {
      return Humans.mergeHuman(payload.humanId, payload.data);
    },
    [actions.deletePhoto](ctx, payload) {
      return Humans.request('delete', payload);
    },
    [actions.resetHuman](ctx) {
      ctx.commit(mutations.resetHuman);
    },
  },
  getters: {
    human(state) {
      return state;
    },
  },
};

