<template>
  <div class="md-toolbar-row">
    <md-button class="md-icon-button md-list-action" @click="onPressBefore">
      <md-icon class="md-primary">navigate_before</md-icon>
    </md-button>
    <div class="md-toolbar-section-end">
      <md-menu md-direction="bottom-start">
        <md-button md-menu-trigger class="md-icon-button md-primary">
          <md-icon class="md-primary">more_horiz</md-icon>
        </md-button>

        <md-menu-content>
          <md-menu-item
            :key="i.value"
            v-for="i in rightMenuItems"
            @click="onPressMenu(i)"
          >
            {{i.label}}
          </md-menu-item>
        </md-menu-content>
      </md-menu>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Header',
  props: {
    rightMenuItems: Array,
  },
  methods: {
    onPressBefore() {
      this.$emit('press-before');
    },
    onPressMenu(menuItem) {
      this.$emit('press-right--menu', menuItem.value);
    },
  },
};
</script>
