<template>
  <md-list>
    <md-subheader class="list-title">Members</md-subheader>
    <md-list-item :key="people.id" v-for="people in peoples" @click="onPressPerson(people)">
      <md-avatar>
        <img :src="`${apiUrl}${people.face}`" alt="People">
      </md-avatar>

      <div class="md-list-item-text">
        <span>{{people.name}}</span>
        <span>{{people.description}}</span>
      </div>

      <md-button class="md-icon-button md-list-action">
        <md-icon class="md-primary">navigate_next</md-icon>
      </md-button>
    </md-list-item>
  </md-list>
</template>

<script>
export default {
  name: 'HumansList',
  props: {
    peoples: Array,
    apiUrl: String,
  },
  methods: {
    onPressPerson(person) {
      this.$emit('press-person', person);
    },
  },
};
</script>
