<template>
  <div class="photos">
    <div
      :class="{photo: true, 'selected-photo': selectedFaces.includes(url) }"
      :key="i - 1"
      v-for="(url, i) in faces"
      @click="onToggleFace(url)"
    >
      <img :src="`${apiUrl}${url}`" alt="People">
    </div>
  </div>
</template>

<script>
export default {
  name: 'PhotoGallery',
  props: {
    apiUrl: String,
    faces: Array,
    selectedFaces: Array,
  },
  methods: {
    onToggleFace(face) {
      const faceIndex = this.selectedFaces.indexOf(face);
      if (faceIndex === -1) {
        this.$emit('update:selectedFaces', [...this.selectedFaces, face]);
        return;
      }
      const faces = [...this.selectedFaces];
      faces.splice(faceIndex, 1);
      this.$emit('update:selectedFaces', faces);
    },
  },
};
</script>

<style>
  .photos {
    display: flex;
    flex-wrap: wrap;
  }

  .photo {
    width: 22vw;
    height: 22vw;
    margin: 1px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }

  .photo>img {
    width: 100%;
    height: 100%;
  }

  .photo.selected-photo::after {
    content: "";
    width: 20px;
    height: 20px;
    border-radius: 10px;
    background: white;
    border: 5px solid var(--md-theme-default-primary-on-background, #448aff);
    display: block;
    position: absolute;
    bottom: 10px;
    right: 10px;
  }
</style>
